import React from 'react'
import { classNames, Tappable } from '@vkontakte/vkui'
import { ICell, TypeRedBlack } from 'types/table'
import { SymbolView } from '../SymbolView/SymbolView'

import './TableCellView.css'
import { getRandomColor, normalizeHEXColor } from 'utils/color'
import { useAppDispatch } from 'hooks/redux'
import { clickCell } from 'store/slices/tableSlice'

interface TableCellViewProps extends ICell {
   // onClick?: () => void
   outline?: 'primary' | 'secondary'
}

export const TableCellView: React.FC<TableCellViewProps> = ({
   id,
   color = getRandomColor(),
   disabledTappable,
   sequenceValue,
   outline = 'primary',
   // animation = 'opacity',
   animation,
   symbol,
   // onClick,
}) => {
   const dispatch = useAppDispatch()
   const onClickCell = () => {
      dispatch(clickCell(sequenceValue))
   }
   // const redOrBlack: TypeRedBlack = Math.random() - 0.5 > 0 ? 'red' : 'black'
   const resolvedBackgroundColor = normalizeHEXColor(color)

   const tappableMode = resolveTableCellTappableMode(resolvedBackgroundColor)

   return (
      <Tappable
         onClick={onClickCell}
         activeMode={tappableMode}
         hoverMode={tappableMode}
         activeEffectDelay={200}
         disabled={disabledTappable}
         className={classNames(
            'TableCell',
            'Tappable--active-opacity',
            outline && 'TableCell--outline',
            outline && `TableCell--outline-${outline}`
         )}
      >
         <div
            style={{ backgroundColor: resolvedBackgroundColor }}
            className={classNames(
               'TableCell__content',
               // 'TableCell__content--rounded',
               // redOrBlack && `TableCell__content--type-${redOrBlack}`,
               animation && `TableCell__content--animation-${animation}`
            )}
         >
            <SymbolView {...symbol} symbolColor='primary' />
         </div>
      </Tappable>
   )
}



type TypeTappableMode = 'opacity' | 'background'
const resolveTableCellTappableMode = (color?: string): TypeTappableMode => {
   if (color !== undefined) return 'opacity'
   return 'background'
}
