export enum TableType {
   NUMBERS = 'numbers',
   CYRILLIC = 'cyrillic',
   LATIN = 'latin',
}

/**возрастающем порядке черные числа, и в убывающем порядке красные */
export enum TableVariant {
   STANDARD = 'standard',
   GORBOV = 'gorbov',
   // PLATONOV = 'platonov',
}
export enum TableMode {
   CLASSIC = 'classic',
   HARD = 'hard',
   CUSTOM = 'custom',
}

export enum TableSequence {
   DEFAULT = 'default',
   RIGHT = 'right',
   RANDOM = 'random',
}

export enum TableColor {
   DEFAULT = 'default',
   RANDOM = 'random',
}

export interface ISettingsCells {
   colorCell: TableColor
   colorSymdol: TableColor

   markSelectedCells: boolean
   showErrors: boolean
}

export interface ITableParams {
   tableType: TableType
   tableVariant: TableVariant
   tableMode: TableMode
   tableSize: number

   tableIsShuffleCells: boolean
   tableIsFlipHorizontally: boolean
   tableIsFlipVertically: boolean
}

export interface ICell {
   id: string
   sequenceValue: string
   symbol: ISymbol

   disabledTappable?: boolean
   color?: string

   animation?: string
   /**для какого то режима */
   effect?: string
}

export interface ISymbol {
   id: string
   value: string

   disabled?: boolean
   color?: string
   animation?: string
   /**для какого то режима */
   isFlipHorizontally?: boolean
   /**для какого то режима */
   isFlipVertically?: boolean
}

export type TypeRedBlack = 'red' | 'black'
